.Nav {
  grid-area: n;
  background-color: white;
  height: 130px;
  border-radius: 8px;
  box-shadow: 0px 0px 1.5px 0px black;
  margin-bottom: 20px;
}

.item {
  font-size: 22px;
  padding-left: 5px;
  letter-spacing: 1px;
  display: inline-block;
  margin-left: 5px;
  font-size: 24px;
}

.activeLink {
  color: rgb(107, 128, 126);
  padding: 5px 5px 5px 0px;
  border-radius: 6px 0px 0px 6px;
}

.item:hover {
  color: rgb(132, 160, 158);
}

.Nav img {
  width: 17px;
  margin-left: 7px;
}

.items {
  margin-top: 5px;
}
