.AvaImg img,
.AvaImgGuest img {
  border-radius: 8px;
  height: 200px;
  width: 200px;
  box-shadow: 0px 0px 0px 0px black;
}

.profileInfo {
  display: grid;
  grid-template-columns: 2fr 10fr;
  gap: 15px;
  background-color: #ebe8e8;
}

.AvaImg {
  background-color: white;
  padding: 10px;
  border-radius: 8px;
  height: 240px;
  box-shadow: 0px 0px 1.5px 0px black;
}

.AvaImgGuest {
  background-color: white;
  padding: 10px;
  height: 200px;
  box-shadow: 0px 0px 2px 0px black;
  border-radius: 8px;
}

.profileForm {
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0px 0px 1.5px 0px black;
}

.userInfo {
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0px 0px 1.5px 0px black;
}

.userInfo div {
  padding: 5px;
  padding-top: 0px;
}

.userInfo .item {
  color: #4084af;
}

.statusitem {
  width: 460px;
  height: 50px;
  display: inline-block;
  word-wrap: break-word;
}

.userName {
  font-size: 25px;
  font-weight: 5px;
  font-weight: bold;
  display: inline;
}

.contacts {
  margin-left: 30px;
}

.contactsHead {
  margin-top: 32px;
  font-size: 20px;
  background-color: rgba(150, 187, 184, 0.8);
  border-radius: 5px;
  cursor: pointer;
  color: white;
}

.contactsHead:hover {
  margin-top: 32px;
  font-size: 20px;
  background-color: rgb(151, 185, 183);
  border-radius: 5px;
  cursor: pointer;
}

.contactsHead img {
  width: 13px;
  margin-left: 590px;
  margin-top: 10px;
}

.contactsHead span {
  font-size: 25px;
}

.addPhoto label {
  cursor: pointer;
  background-color: rgba(150, 187, 184, 0.8);
  color: white;
  display: inline-block;
  margin-top: 5px;
  padding: 3px;
  width: 192px;
  text-align: center;
  font-size: 17px;
  font-weight: bold;
  border-radius: 5px;
  font-size: large;
}

.addPhoto label:hover {
  background-color: rgb(151, 185, 183);
  box-shadow: 0px 0px 1px 0px black;
}

.AvaImg input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  z-index: -1;
}

.editButton {
  color: #5da8d6;
  cursor: pointer;
  margin-right: 5px;
  margin-top: 10px;
  float: right;
}

.menu {
  display: flex;
  margin-top: 20px;
}

.menu img {
  width: 25px;
  height: 25px;
  background-color: rgb(151, 185, 183, 0.5);
  padding: 7px;
  border-radius: 35%;
}

.menu .items {
  padding: 5px 5px 1px 5px;
  background-color: #fff;
  border-radius: 20px;
  margin-left: 14px;
}

.menu img:hover {
  background-color: rgb(151, 185, 183, 0.7);
  box-shadow: 0px 0px 2px 0px black;
}

.editButton:hover {
  color: #4586ad;
}

.formInputs {
  margin-bottom: 10px;
}

.profileForm .item {
  font-weight: bold;
  color: rgb(0, 0, 0, 0.4);
}

.profileForm label {
  float: right;
  color: #79bbe4;
  cursor: pointer;
}

.profileForm label:hover {
  color: #4586ad;
}
